<template>
  <div>
    <div>当前第{{currentPage}}页</div>
    <nav aria-label="Page navigation example" class="mt-4 spiritPagination">
      <ul class="pagination justify-content-center">
        <li class="page-item disabled" @click="prevOrNext(-1)">
          <a class="page-link" href="javascript:void(0);" aria-label="Previous">
            <span aria-hidden="true">&laquo;</span>
            <span class="sr-only">Previous</span>
          </a>
        </li>
        <li class="page"
            v-for="(item, index) in pages"
            :key="index"
            @click="select(item)">
          <a class="page-link" :class="{actived: item === currentPage}" href="javascript:void(0);">{{item}}</a>
        </li>
        <li class="page-item" @click="prevOrNext(1)">
          <a class="page-link" href="javascript:void(0);" aria-label="Next">
            <span aria-hidden="true">&raquo;</span>
            <span class="sr-only">Next</span>
          </a>
        </li>
      </ul>
    </nav>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        currentPage: 1,
        totalPages: 6
      }
    },
    computed: {
      pages() {
        const c = this.currentPage
        const t = this.totalPages
        if (c <= 5) {
          return [1, 2, 3, 4, 5, 6, 7, 8, 9, '...', t] //第一种情况
        } else if (c >= t - 4) {
          return [1, '...', t - 8, t - 7, t - 6, t - 5, t - 4, t - 3, t - 2, t - 1, t] //第二种情况
        } else {
          return [1, '...', c - 3, c - 2, c - 1, c, c + 1, c + 2, c + 3, '...', t] //第三种情况
        }
      }
    },
    methods: {
      select(n) {
        if (n === this.currentPage) return
        if (typeof n === 'string') return
        this.currentPage = n
      },
      prevOrNext(n) {
        this.currentPage += n
        this.currentPage < 1
          ? this.currentPage = 1
          : this.currentPage > this.totalPages
          ? this.currentPage = this.totalPages
          : null
      }
    }
  }
</script>
<style>
  .actived {
    border-color: #2d8cf0;
    background-color: #2d8cf0;
    color: #fff;
  }
</style>



